import { ADD_TO_CART, CLEAR_CART, REMOVE_FROM_CART } from "../actions/types";

export default function cartReducer(state, action) {
  console.log("state : " + state);
  console.log("action : " + action);
  console.log(state, action);

  let addFunction = () => {
    const product = action.productInfo;
    const quantity = action.quantity;
    const new_state = { ...state };
    const { cart } = new_state;
    const length = cart.length;
    let exist = false;

    for (let i = 0; i < length; i++) {
      if (cart[i].product.id === product.id) {
        exist = true;
        cart[i].product = product;
        cart[i].quantity = parseInt(quantity) + parseInt(cart[i].quantity);
      }
    }

    if (exist) {
      return new_state;
    } else {
      return {
        cart: [
          ...state.cart,
          {
            product: action.productInfo,
            quantity: action.quantity,
          },
        ],
      };
    }
  };
  switch (action.type) {
    case ADD_TO_CART: {
      return addFunction();
    }

    case REMOVE_FROM_CART: {
      const item_index = action.index;
      const new_state = { ...state };
      new_state.cart.splice(item_index, 1);

      return new_state;
    }

    case CLEAR_CART: {
      const new_state = { ...state };
      new_state.cart = [];

      return new_state;
    }

    default:
      return state;
  }
}
