import React from "react";
import { Component } from "react";
import { NavLink } from "react-router-dom";
import Scrollbar from "react-scrollbars-custom";

class ProductItem extends Component {
  render() {
    const MyNavLink = (props) => (
      <NavLink activeClassName="active" className="navlink" {...props}>
        {props.children}
      </NavLink>
    );
    const product = this.props.item.product;
    const quantity = this.props.item.quantity;
    return (
      <div className="card">
        <img src={product.image} className="card-img-top" alt="..." />
        <div className="card-body">
          <h4>{product.name}</h4>
          <p className="quantity">
            Available : <span>{quantity}</span>
          </p>
          <h5 className="card-title">
            Price:
            <div className="price">
              {product.price}
              <span>$</span>
            </div>
          </h5>
          <Scrollbar>
            <p className="card-text">{product.description}</p>
          </Scrollbar>
          <MyNavLink to={`/products/${product.id}`} className="btn btn-primary">
            Purchase Details
            <i className="fas fa-cart-plus"></i>
          </MyNavLink>
        </div>
      </div>
    );
  }
}
export default ProductItem;
