import React from "react";
import { Component } from "react";
import { connect } from "react-redux";
import { getById } from "../api/products";
import { addToCart } from "../store/actions/actions";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Scrollbar from "react-scrollbars-custom";

const MySwal = withReactContent(Swal);

class Product extends Component {
  static propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
  };
  constructor(props) {
    super(props);
    this.state = {
      productItem: {},
      amount: 0,
      laoding: true,
    };
  }

  componentDidMount() {
    // console.log(this);  // to Get the path of The id in {this}
    const id = this.props.match.params.id;
    getById(parseInt(id)).then((productItem) => {
      setTimeout(() => {
        this.setState({
          productItem,
          laoding: false,
        });
      }, 200);
    });
  }

  getCartItemAmount = (id) => {
    const cartArray = this.props.cartItems;
    let itemAmount = 0;
    for (let i = 0; i < cartArray.length; i++) {
      if (cartArray[i].product.id === id) {
        itemAmount = cartArray[i].quantity;
      }
    }
    return itemAmount;
  };

  handleAmount = (event) => {
    const value = event.target.value;

    const realAvailable =
      this.state.productItem.quantity -
      this.getCartItemAmount(this.state.productItem.product.id);
    if (value < 0 || value > realAvailable) {
      return;
    }
    this.setState({
      amount: value,
    });
  };

  handleAddToCart = (product, e) => {
    if (this.state.amount === 0) {
      MySwal.fire({
        title: <p>Quantity Can't be 0</p>,
        position: "center",
        icon: "warning",
        iconColor: "red",
        showConfirmButton: false,
        timer: 4000,
      });
    } else {
      this.props.addToCart(product, this.state.amount);
      MySwal.fire({
        title: <p>Your T-shirt Added To Cart</p>,
        position: "center",
        icon: "success",
        iconColor: "green",
        showConfirmButton: false,
        timer: 2000,
      }).then(() => {
        this.props.history.replace("/products");
      });
    }
  };

  render() {
    if (this.state.laoding) {
      return "Loading ..";
    }
    // const MyNavLink = (props) => (
    //   <NavLink activeClassName="active" className="navlink" {...props}>
    //     {props.children}
    //   </NavLink>
    // );

    const product = this.state.productItem.product;
    const availQuantity = this.state.productItem.quantity;
    const amount = this.state.amount;
    return (
      <div id="product" className="container col-7">
        <div className="card">
          <h4>T-Shirt Info.</h4>
          <div className="card-section">
            <img src={product.image} alt="..." />
            <div className="card-body">
              <h4>{product.name}</h4>
              <h5 className="card-title">
                Price:
                <div className="price">
                  {product.price}
                  <span>$</span>
                </div>
              </h5>
              <Scrollbar>
                <p className="card-text">{product.description}</p>
              </Scrollbar>
              <div className="available">
                Available :
                <span>
                  {availQuantity - this.getCartItemAmount(product.id)}
                </span>
              </div>
              <div className="amount-container">
                <p>Total : {amount * product.price} $</p>
                <div className="amount">
                  <input
                    type="number"
                    value={amount}
                    className="form-control"
                    onChange={this.handleAmount}
                  ></input>
                  <Link
                    to={`/products/${this.state.productItem.product.id}`}
                    id="add-product-to-card"
                    className="btn btn-primary"
                    onClick={() => this.handleAddToCart(product)}
                  >
                    Add To Cart
                    <i className="fas fa-cart-plus"></i>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    cartItems: state.cart,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addToCart: (serviceInfo, quantity) =>
      dispatch(addToCart(serviceInfo, quantity)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Product);
